.btn {
	border-radius: 25px;
	position: relative;
	padding: 17px 20px 15px;
	font-size: 16px;
	line-height: 18px;
	position: relative;
	transition: all $transition;
	&:focus {
		box-shadow: 0 0 0 .25rem rgba(0, 0, 0, 0);
	}
	&-white {
		background-color: $white;
	}
	&-icon {
		border-radius: 50px;
		padding: 13px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		i {
			font-size: 22px;
			transition: opacity $transition;
		}
		&:hover {
			i {
				opacity: 0.5;
			}
		}
	}
	&-has-dropdown {
		box-shadow: 0px 30px 60px rgba(0, 0, 0, 0);
		transition: box-shadow $transition, border-radius $transition;
		.btn-dropdown {
			position: absolute;
			background-color: #fff;
			width: 100%;
			top: 100%;
			left: 0;
			border-bottom-left-radius: 25px;
			border-bottom-right-radius: 25px;
			padding-left: 20px;
			padding-right: 20px;
			transform: translateY(-20px);
			opacity: 0;
			visibility: hidden;
			transition: all $transition, opacity .15s;
			border: 1px solid transparent;
			z-index: 1;
		}
		&:hover {
			.btn-dropdown {
				opacity: 1;
				visibility: visible;
				transform: translateY(0);
			}
		}
	}
	&-tobar__phone {
		padding-right: 56px;
		display: flex;
		align-items: center;
		border: none;
		a {
			display: block;
			text-align: left;
			color: $black;
			text-decoration: none;
			transition: color $transition;
		}
		.topbar__phone-dropdown {
			a {
				margin-bottom: 17px;
			}
		}
		i {
			position: absolute;
			right: 20px;
			transform: rotate(180deg);
			transition: transform $transition;
		}
		&:hover {
			border-bottom-right-radius: 0;
			border-bottom-left-radius: 0;
			box-shadow: 0px 30px 60px rgba(0, 0, 0, 0.2);
			a {
				&:hover {
					color: $green-accent;
				}
			}
			i {
				transform: rotate(0);
			}
		}
	}
	&-login {
		a {
			display: block;
			text-align: left;
			margin-bottom: 20px;
			text-decoration: none;
			color: $black;
			transition: color $transition;
			&:hover {
				color: $green-accent;
			}
		}
		.btn-dropdown {
			padding-top: 20px;
			top: -1px;
			left: -1px;
			min-width: 210px;
			border-radius: 25px;
			box-shadow: 0px 30px 60px rgba(0, 0, 0, 0.2);
		}
	}
	&-topbar__cart {
		padding-left: 80px;
		padding-right: 44px;
		text-transform: uppercase;
		font-weight: bold;
		letter-spacing: 0.05em;
		border: none;
		span {
			transition: opacity $transition;
			&:first-of-type {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				border: 2px solid $green-accent;
				position: absolute;
				left: 0;
				top: 0;
				width: 50px;
				height: 100%;
				border-radius: 50px;
				font-weight: bold;
			}
		}
		&:hover {
			span {
				&:last-of-type {
					opacity: .5;
				}
			}
		}
	}
	&-border {
		border: 2px solid transparent;
		border-radius: 50px;
	}
	&-green {
		border-color: $green-accent;
		text-transform: uppercase;
		font-weight: bold;
		letter-spacing: 0.05em;
		&:hover,
		&.active {
			background-color: $green-accent;
			color: $white;
		}
	}
	&-turq {
		background-color: $turquoise;
		&:hover {
			background-color: darken($turquoise, 5%);
		}
	}
}

/** Category nav tabs button **/

.category-btn {
	color: $white;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 16px;
	line-height: 18px;
	letter-spacing: 0.05em;
	border: 2px solid $white;
	border-radius: 50%;
	width: 140px;
	height: 140px;
	text-align: center;
	background-color: $black;
	transition: color $transition;
	padding: 10px;
	word-wrap: break-word;
	&:hover,
	&:focus {
		color: $green-accent;
	}
	@media (max-width: 1100px) {
		width: 100px;
		height: 100px;
		font-size: 14px;
		line-height: 1;
	}
}